import React, { useState, useEffect, Fragment } from 'react';
import bridge from '@vkontakte/vk-bridge';
import '@vkontakte/vkui/dist/vkui.css';
import './css/FontApp.css';
import {
	AppRoot, ConfigProvider, AdaptivityProvider,
	SplitLayout, SplitCol, View, Panel, PanelHeader,
	Root, Group, Avatar, Snackbar, Div, IconButton, 
	FixedLayout, Separator, Tabs, TabsItem, Counter, 
	Text, Tabbar, TabbarItem, Banner, Footer, Header,
	PopoutWrapper, Placeholder, MiniInfoCell, ScreenSpinner,
	Spinner, Button, Link, ModalRoot, ModalCard, Spacing
} from "@vkontakte/vkui";

import {
	Icon20CancelCircleFillRed, Icon24BrowserBack, Icon28CubeBoxOutline,
	Icon28More, Icon28HomeOutline, Icon24Dismiss, Icon20CloudArrowUpOutline,
	Icon56FileBrokenOutline, Icon24PictureInPictureOut16x9Outline,
	Icon56ArrowRightDoorOutline, Icon28SunOutline, Icon28MoonOutline
} from "@vkontakte/icons"; 

const appIcon = 'https://psv4.userapi.com/c237131/u363163789/docs/d57/9c723fde8bb9/appIcon_-_576kh576.png?extra=4xKh99PkQKqXCPqoGEhKQgxewBjg4oavPPR05IJqHUdGKeqfpsOWRGW-spH9muuWg5b1qixzxe-jwQIdLhGsaPq8DjDAOO7vE0s5RZO-z5B7npY1i2Eu-PMfDs2R1K47fB8GSfx2yI3CltVS8alNiiYR';

// MODALS NAMES
const MODAL_CARD_APP_CHANGE_THEME = "modalCardAppChangeTheme";
const MODAL_CARD_APP_SNACKBAR = "modalCardAppSnackbar";

// SNACKBARS NAMES
const POPOUT_VERSION_APP = "popoutVersionApp";


const access = [
	363163789 // Я
];

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			// APP_INFO
			lights: ["bright_light", "space_gray"],
			activeView: 'errors',
			activePanel: 'whitePage',
			ls: null,
			activeAppTheme: "bright_light",
			activeTab: 'updates',

			snackbar: null,

			popout: null,
			popoutHistory: [],

			activeModal: null,
			modalHistory: [],

			// USER_INFO
			user: null,
			id: null,

			error: null,

			buttonShowError: false
		}

		this.sic = {
			icmSG: "white", // Icons More Space Gray
			icmBL: "black",  // Icons More Bright Light
		}

		this.modalBack = () => {
			this.setActiveModal(
				this.state.modalHistory[this.state.modalHistory.length - 2]
			);
		};

		this.popoutBack = () => {
			this.setActivePopout([this.state.modalHistory.length - 2]);
		};
  
		this.replButtonError = this.replButtonError.bind(this);
	}

	goPanel(panel) {
		this.setState({ activePanel: panel });
	}

	goPage(view, panel) {
		this.setState({ activeView: view, activePanel: panel });
	}

	sendPush(text, icon) {
		if (icon == "cancel") icon = <Icon20CancelCircleFillRed width={26} height={26} />;

		this.setState({
			snackbar: (
				<Snackbar
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={
						<Avatar size={26} >
							{icon}
						</Avatar>
					}
				>
					{text}
				</Snackbar>
			),
		});
	}

	replButtonError() {
		if(this.state.buttonShowError == true) {
			this.setState({buttonShowError: false});
		} 
		if(this.state.buttonShowError == false) {
			this.setState({buttonShowError: true});
		}
	}

	setPopout(type, text) {
		if(type == "getVersionInfo") {
			this.setState({snackbar: null, popout: <PopoutWrapper onClick={() => this.setState({popout: null})}>
				<div
				  style={{
					backgroundColor: "var(--background_content)",
					borderRadius: 8,
					width: "85%",
					position: "relative",
					padding: "12px",
				  }}>
					<Placeholder style={{fontSize: "18px"}} icon={<Icon28CubeBoxOutline width={56} height={56}/>}>
						Информация о текущей версии приложения            
					</Placeholder>

				  	<Group>
					  	<MiniInfoCell before={<Icon24PictureInPictureOut16x9Outline />} after={<Counter size="s">Beta</Counter>} textWrap="full">
							<Text style={{fontSize: "14px"}}> Версия приложения: 1.0.0 </Text> 
						</MiniInfoCell> 

						<MiniInfoCell before={<Icon20CloudArrowUpOutline width={24} height={24} />} textWrap="full">
							<Text style={{fontSize: "14px"}}> Дата обновления: 24.02.2022 </Text> 
						</MiniInfoCell>
				  	</Group>
				</div>
			  </PopoutWrapper>});
		}

		if(type == "loadingPage") {
			this.setState({snackbar: null, popout: <PopoutWrapper>
				<div
				  style={{
					backgroundColor: "var(--background_content)",
					borderRadius: 8,
					width: "auto",
					position: "relative",
					padding: "12px",
				  }}>
					  <Div>
					  		<Spinner size="large" style={{ margin: "20px 0" }} /> 
						  	<Text style={{ textAlign: "center", "color": "gray" }}> {text} </Text>
					  </Div>
					  
				</div>
			  </PopoutWrapper>
			});
		}
	}

	changeAppTheme() {
		if(this.state.activeAppTheme == "bright_light") {
			this.setState({activeAppTheme: "space_gray"});
		} else {
			this.setState({activeAppTheme: "bright_light"});
		}
	}

	setSheme(sheme, needChange = false) {
		let isLight = this.state.lights.includes(sheme);

		if(needChange) {
			isLight = !isLight;
		}

		this.setState({activeAppTheme: isLight ? "bright_light" : "space_gray"});

		bridge.send('VKWebAppSetViewSettings', {
			'status_bar_style': isLight ? "dark" : "light",
			'action_bar_color': isLight ? '#FFF' : '#000'
		});
	}

	setActivePopout(popout) {
		this.setState({snackbar: null});

		if(popout == POPOUT_VERSION_APP) {
			popout = (<PopoutWrapper onClick={() => this.setActivePopout(null)}>
			<div
			  style={{
				backgroundColor: "var(--background_content)",
				borderRadius: 8,
				width: "85%",
				position: "relative",
				padding: "12px",
			  }}>
				<Placeholder style={{fontSize: "18px"}} icon={<Icon28CubeBoxOutline width={56} height={56}/>}>
					Информация о текущей версии приложения            
				</Placeholder>

				  <Group>
					  <MiniInfoCell before={<Icon24PictureInPictureOut16x9Outline />} after={<Counter size="s">Beta</Counter>} textWrap="full">
						<Text style={{fontSize: "14px"}}> Версия приложения: 1.0.0 </Text> 
					</MiniInfoCell> 

					<MiniInfoCell before={<Icon20CloudArrowUpOutline width={24} height={24} />} textWrap="full">
						<Text style={{fontSize: "14px"}}> Дата обновления: 24.02.2022 </Text> 
					</MiniInfoCell>
				  </Group>
			</div>
		  </PopoutWrapper>);
		}

		popout = popout || null;
		let popoutHistory = this.state.popoutHistory
		? [...this.state.popoutHistory]
		: [];

		if(popout == null) {
			popoutHistory: [];
		} else if (popoutHistory.indexOf(popout) !== -1) {
			popoutHistory = popoutHistory.splice(
				0,
				popoutHistory.indexOf(popout) + 1
			);
		} else {
			popoutHistory.push(popout);
		}

		this.setState({
			popout,
			popoutHistory,
		});
	}

	setActiveModal(activeModal) {
		this.setState({snackbar: null});

		activeModal = activeModal || null;
		let modalHistory = this.state.modalHistory
			? [...this.state.modalHistory]
			: [];

		if (activeModal === null) {
			modalHistory = [];
		} else if (modalHistory.indexOf(activeModal) !== -1) {
			modalHistory = modalHistory.splice(
				0,
				modalHistory.indexOf(activeModal) + 1
			);
		} else {
			modalHistory.push(activeModal);
		}

		this.setState({
			activeModal,
			modalHistory,
		});
	}

	quotApp() {
		bridge.send("VKWebAppClose");
	}

	componentDidMount() {
		this.setPopout("loadingPage", "Получаем данные о Вашем профиле...");

		if(window.location.pathname == "/getAccess") {
			setTimeout(() => {
				// this.setState({ user: 363163789, id: 363163789, popout: null });
				// this.goPage("home", "main");
				this.setState({ popout: null, error: "Error 1: The user is not found in the system. Access denied." })
					this.goPage("errors", "main");
			}, 2000);
		}

		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				this.setSheme(data.sheme);
			}
		});

		bridge.send('VKWebAppGetUserInfo')
			.then(data => {
				setTimeout(() => {
					this.setState({ user: data, id: data.id, popout: null });
					if (~access.indexOf(data.id)) {
						// this.goPage("home", "main");
						bridge.subscribe(({ detail: { type, data } }) => {
							if (type === "VKWebAppChangeFragment") {
								this.goPage("home", "main");
								this.setState({ lc: data.location })
								bridge.send("VKWebAppSetLocation", {"location": ""});
							}
						});
					} else {
						this.setState({ error: "Error 1: The user is not found in the system. Access denied." })
						this.goPage("errors", "main");
					}
				}, 2000);
			})
			.catch(err => {
				setTimeout(() => {
					this.setState({ activeView: "errors", activePanel: "main", error: err });
				}, 2000);
			});

		// Error 1: The user is not found in the system. Access denied. IS ID
		// Error 2: Further work is not possible without your data. Access denied. NOT IS ID
		// Error 
	}

	render() {
		const modal = (<ModalRoot
				activeModal={this.state.activeModal}
				onClose={this.modalBack}
			>
				<ModalCard
					id={MODAL_CARD_APP_CHANGE_THEME}
					onClose={() => this.setActiveModal(null)}
					icon={
						(() => {
							if (this.state.activeAppTheme == "space_gray") {
								return <Icon28MoonOutline width={50} height={50} />;
							} else {
								return <Icon28SunOutline width={50} height={50} />;
							}
						})()
					}
					header="Смена темы приложения"
					subheader={<Text>У вас включена <b> {(() => { return this.state.activeAppTheme.replace(/bright_light/gi, 'светлая').replace(/space_gray/gi, 'тёмная'); })()} </b> тема. </Text>}
					actions={
						<Button
							size="m"
							mode={(() => {
								if(this.state.activeAppTheme == "bright_light") {
									return "accent";
								} else {
									return "accent";
								}
							})()}
							appearance={(() => {
								if(this.state.activeAppTheme == "bright_light") {
									return "light";
								} else {
									return "dark";
								}
							})()}
							onClick={() => this.changeAppTheme()}
						>
							Изменить
						</Button>
					}
				></ModalCard>
				<ModalCard
					id={MODAL_CARD_APP_SNACKBAR}
					onClose={() => this.setActiveModal(null)}
					header="Смена темы приложения"
					subheader={<Text>У вас включена <b> {(() => { return this.state.activeAppTheme.replace(/bright_light/gi, 'светлая').replace(/space_gray/gi, 'тёмная'); })()} </b> тема. </Text>}
					actions={
						<Button
							size="m"
							mode={(() => {
								if(this.state.activeAppTheme == "bright_light") {
									return "accent";
								} else {
									return "accent";
								}
							})()}
							appearance={(() => {
								if(this.state.activeAppTheme == "bright_light") {
									return "light";
								} else {
									return "dark";
								}
							})()}
							onClick={() => this.changeAppTheme()}
						>
							Изменить
						</Button>
					}
				></ModalCard>
			</ModalRoot>
		);

		return (
			<ConfigProvider scheme={this.state.activeAppTheme}>
				<AdaptivityProvider>
					<AppRoot>
						<SplitLayout popout={this.state.popout} modal={modal}>
							<SplitCol>
								<Root activeView={this.state.activeView} style={{ marginBottom: "3%" }}>

									<View id="home" activePanel={this.state.activePanel}>
										<Panel id="main">
											<PanelHeader>Главная</PanelHeader>
											<Group>
												<Placeholder icon={<Icon56FileBrokenOutline width={58} height={58} />}>
													<Text style={{ fontSize: "19px" }}>Ведутся тех. работы</Text>
												</Placeholder>
											</Group>

											<Group>
												<Div style={{margin: "4%"}}>
													{this.state.lc}
												</Div>
											</Group>
										</Panel>
									</View>

									<View id="more" activePanel={this.state.activePanel}>
										<Panel id="main">
											<PanelHeader>Ещё</PanelHeader>

											<Group header={<Header style={{ color: "gray" }}>Смена темы</Header>}>
												<Banner
													header="Сменить тему приложения"
													asideMode="expand"
													onClick={() => this.setActiveModal(MODAL_CARD_APP_CHANGE_THEME)}
													before={(() => {
															if (this.state.activeAppTheme == "space_gray") {
																return <Icon28MoonOutline fill={this.sic.icmSG} width={30} height={30} />;
															} else {
																return <Icon28SunOutline fill={this.sic.icmBL} width={30} height={30} />;
															}
														})()}
												/>
											</Group>

											<Group header={<Header style={{ color: "gray" }}>Версии</Header>}>
												<Banner
													header="Версии ChatManager Bot"
													subheader="Новые и прошлые версии чат-менеджера"
													asideMode="expand"
													onClick={() => this.sendPush("Невозможно перейти на несуществующую страницу.", "cancel")}
													before={<Icon28CubeBoxOutline fill={(() => { if(this.state.activeAppTheme == "bright_light") {return this.sic.icmBL} else {return this.sic.icmSG} })()} width={30} height={30} />}
												/>
												<Banner
													header="Версия приложения"
													subheader="Информация о текущей версии приложения"
													asideMode="expand"
													onClick={() => this.goPage("more", "appInfo")}
													before={<Icon28CubeBoxOutline fill={(() => { if(this.state.activeAppTheme == "bright_light") {return this.sic.icmBL} else {return this.sic.icmSG} })()} width={30} height={30} />}
												/>
											</Group>

											<Footer>Версия приложения: 1.0.0</Footer>
											<Spacing size={16} />
										</Panel>

										<Panel id="updateV">
											<PanelHeader left={<IconButton onClick={() => this.goPanel("main")} >
												<Icon24BrowserBack fill="612db5" />
											</IconButton>}>UpVs</PanelHeader>

											<Group>
												<Div>
													{(() => {
														if (this.state.activeTab == "updates") {
															return <Div style={{ color: "gray" }}>Обновлений нет</Div>;
														} else {
															return <Div style={{ color: "gray" }}>Исправления есть!</Div>;
														}
													})()}
												</Div>

												<FixedLayout filled vertical="bottom">
													<Separator wide />
													<Tabs>
														<TabsItem
															selected={this.state.activeTab === "updates"}
															onClick={() => this.setState({ activeTab: "updates" })}
															after={<Counter mode="prominent" size="s">+</Counter>}
														>
															Обновления
														</TabsItem>
														<TabsItem
															selected={this.state.activeTab === "corrections"}
															onClick={() => this.setState({ activeTab: "corrections" })}
															after={<Counter mode="primary" size="s">-</Counter>}
														>
															Исправления
														</TabsItem>
													</Tabs>
												</FixedLayout>
											</Group>
										</Panel>

										<Panel id="appInfo">
											<PanelHeader left={<IconButton onClick={() => this.goPage("more", "main")}>
												<Icon24BrowserBack fill="612db5" />
											</IconButton>}> Информация
											</PanelHeader>

											<Group style={{ "margin": "2%" }}>
												<Div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
													<Avatar mode="image" size={96} src={appIcon} />
												</Div>
												<MiniInfoCell before={<Icon24PictureInPictureOut16x9Outline />} after={<Counter size="s">Beta</Counter>} textWrap="full">
													<Text style={{ fontSize: "20px" }}> Версия приложения: 1.0.0 </Text>
												</MiniInfoCell>

												<MiniInfoCell before={<Icon20CloudArrowUpOutline width={24} height={24} />} textWrap="full">
													<Text style={{ fontSize: "20px" }}> Дата обновления: 24.02.2022 </Text>
												</MiniInfoCell>
											</Group>
										</Panel>
									</View>

									<View id="errors" activePanel={this.state.activePanel}>

										<Panel id="main">
											<PanelHeader>Chat Manager</PanelHeader>
											<div style={{
												backgroundColor: "var(--background_content)",
												borderRadius: 8,
												width: "auto",
												verticalAlign: "center",
												position: "relative",
												display: "flex",
												flexDirection: "column",
												justifyContent: "center",
												alignItems: "center",
												textAlign: "center",
												padding: "12px",
											}}>
												<Div>
													<Icon24Dismiss width={68} height={68} fill="#ff0f00d1" />
												</Div>

												<Div>
													<Text style={{ textAlign: "center", "color": "gray", fontSize: "19px" }}> Произошла ошибка при выдаче прав доступа. </Text>
												</Div>

												<Div>
													<Button size="m" mode="secondary" onClick={this.replButtonError}>
														{this.state.buttonShowError ? "Скрыть ошибку" : "Показать ошибку"}
													</Button>
												</Div>

												{(() => {
													if (this.state.buttonShowError) {
														return <React.Fragment>
															<Separator style={{ margin: "12px 0" }} />
															<Div style={{ borderRadius: "8px", background: "#eb776e76" }}>
																<Text>
																	{this.state.error}
																</Text>
															</Div>
															<Div>
																<Link href="https://vk.com/im?sel=-207901232" target="_blank"> <b>Написать в тех. поддержку</b> </Link>
															</Div>
														</React.Fragment>;
													} else {
														return;
													}
												})()}
											</div>
											<FixedLayout vertical='bottom'><Div>
												<Button after={<Icon56ArrowRightDoorOutline width={24} height={24} />} style={{ "color": "#eb4b3fd8" }} size="l" mode="overlay" stretched onClick={this.quotApp}> Выйти из приложения </Button></Div>
											</FixedLayout>
										</Panel>

										<Panel id="whitePage">
											<PanelHeader> Chat Manager </PanelHeader>
										</Panel>

									</View>
								</Root>

								{this.state.snackbar}

								{(() => {
									if (this.state.activePanel != "updateV" & this.state.activeView != "errors") {
										return <FixedLayout vertical="bottom"><Tabbar
											itemsLayout="horizontal"
											style={{ position: "static" }}
										>
											<TabbarItem
												style={{ cursor: "pointer" }}
												selected={this.state.activeView == "home"}
												onClick={() => this.goPage("home", "main")}
												text="Главная"
											>
												<Icon28HomeOutline />
											</TabbarItem>
											<TabbarItem
												style={{ cursor: "pointer" }}
												selected={this.state.activeView == "more"}
												onClick={() => this.goPage("more", "main")}
												text="Ещё"
											>
												<Icon28More />
											</TabbarItem>
										</Tabbar></FixedLayout>;
									} else {
										return;
									}
								})()}
							</SplitCol>
						</SplitLayout>
					</AppRoot>
				</AdaptivityProvider>
			</ConfigProvider>
		);
	}
}

export default App;
